const TempComp = ({ nextClick, swipeDirecion, prevClick, setNextClick, setPrevClick }) => {
    const swiper = useSwiper();
    console.log(nextClick)
    console.log(prevClick)
    if(swipeDirecion === 'right') {
        swiper.slideNext(200)
    }

    console.log(swipeDirecion)

    if(swipeDirecion === 'left') {
        swiper.slidePrev(200)
    }



    return <button onClick={() => swiper.slideNext()}>Slide to the next slide</button>

}

